.sign-in-row{
    padding: 50px;

    & input{
        margin: 0 20px;
    } 
    & i{
        margin: 20px;
    }
    & label {
        margin: 10px;
    }
    & div.ant-form-explain {
        margin: 0 20px 10px;
    }
    & .submit-btn {
        & i{
            margin: 0px;
        }
    }
}

